.Article {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1rem;     
}

@media (max-width: 768px) {
    .Article {
        grid-template-columns: repeat(2, 1fr);
    }
} 

@media (max-width: 600px) {
    .Article {
        grid-template-columns: 1fr;
    }
} 